.exitLogin{
    width: 90%;
    height: 44px;
    line-height: 44px;
    color: #fff;
    font-size: 14px;
    text-align: center;
    border-radius: 3px;
    background: #252525;
    margin:0 auto;
    
    margin-top: 20px;
    margin-bottom: 20px;
}
.ownMessageInputWrap{
    background:#fff
}
.ownMessageInput{
    margin-left:12px;
    margin-right:12px;
    padding-top:10px;
    padding-bottom:10px;
    border-bottom:1px solid #f9f9f8;
    position:relative;
}
.inputTop{
    margin-bottom:5px;
    color:#333333;
    font-size:14px;
}
.ownMessageInput input{
    font-size:14px;
    color:#cccccc;
}
.ownMessageInput font{
    font-size:12px;
    margin-left:5px;
}
.sDiv{
    padding-top: 3px;
    padding-bottom: 3px;
    padding-left: 10px;
    padding-right: 8px;
    background: #252525;
    font-size: 12px;
    color: #fff;
    vertical-align: middle;
    display: inline-block;
    position: absolute;
    right: 0;
    top: 32px;
    border-radius: 5px;
}
.sDiv span{
    vertical-align: middle;
}
.sDiv img{
    width:8px;
    margin-left:5px;
    vertical-align: middle;
}
.login_wrap{
    background: #f9f9f8;
}
.btt{
    border-top:4px solid #f9f9f9;
}
.bttTop{
    vertical-align: middle;
    padding-top: 6px;
    padding-bottom: 4px;
    padding-left: 10px;
}


.bttTop img{
    width: 16px;
    margin-right: 4px;
    vertical-align: middle;
}
.bttTop span{
    font-size: 14px;
    color: #333;
    font-weight: bold;
    vertical-align: middle;
}
.pinkBg{
    background: #FAAAAA;
}
.dateWrap{
    height: 30px;
    position: relative;
    font-size: 12px;
    vertical-align: middle;
}
.dateWrap>span{
    position: relative;
    top:3px;
    margin-left: 3px;
    margin-right: 3px;
}
.sDiv_1{
    padding-top: 0px;
    padding-bottom: 4px;
    padding-left: 10px;
    padding-right: 8px;
    background: #252525;
    font-size: 12px;
    color: #fff;
    vertical-align: middle;
    display: inline-block;
    border-radius: 5px;
    vertical-align: middle;
}
.sDiv_1 span{
    vertical-align: middle;
    position: relative;
    top:2px;
}
.sDiv_1 img{
    width:8px;
    margin-left:5px;
    vertical-align: middle;
    position: relative;
    top:2px
}
.menuDiv {
    width: 100%;
    position: absolute;
    top: 25px;
    left: 0;
    z-index: 9999;
    background: #252525;
}

.menuDiv div{
    padding-top: 3px;
    padding-bottom: 3px;
    border: 1px solid #f9f9f9;
    font-size: 12px;
    color: #ccc;
    text-align: center;
}