.backHeader{
    border-bottom: 1px solid #f9f9f8;
}
.searchWrap{
    padding-top:10px;
    padding-bottom:8px;
    background:#fff;
    padding-left:10px;
    position:relative;
    border-bottom:6px solid #f9f9f8
}
.searchInner{
    width: 82%;
    height: 35px;
    border-radius: 35px;
    padding-left: 16px;
    background: rgba(246,246,246,1);
}
.searchInner img{
    width:14px;
    padding-top:11px;
    margin-right:10px;
    float: left;
}
.searchInner input{
    height: 30px;
    width: 80%;
    color: #9d9d9d;
    font-size: 12px;
    float: left;
    position: relative;
    top: 3px;
}
.cancel{
    color:#999999;
    font-size:14px;
    position:absolute;
    right:10px;
    top:18px
}
.searchTitleWrap{
    padding:12px;
    background:#fff;
    border-bottom:6px solid #f9f9f8;
}
.searchTitleInnerWrap{
    overflow:hidden;
}
.searchTitleH{
    color: #333333;
    font-size: 14px;
    font-weight: bold;
    margin-top: 3px;
    margin-bottom: 11px;
}
.searchTitleInner {
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 19px;
    color: #666666;
    font-size: 12px;
    margin-right: 8px;
    float: left;
    background: #f5f7f8;
    margin-bottom: 10px;
}
.searchDetail{
    width:16.6%;
    height:80px;
    text-align: center;
    margin-bottom: 5px;
    font-size: 12px;
    color:#666666;
    display: inline-block;
    float: left;
}
.searchDetail img{
    width:40px;
    margin-bottom: 1px;
}
.bt0{
    border-bottom: 0;
}
.cancelSearch{
    padding-top:2px;
    padding-bottom: 2px;
    padding-left: 10px;
    padding-right: 10px;
    background: #000000;
    color: #fff;
    border-radius: 10px;
}


.searchRes{
    padding-top: 10px;
    padding-bottom: 10px;
    background: #fff;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    color: #666666;
    vertical-align: middle;
}
.searchRes img{
    width:28px;
    vertical-align: middle;
    margin-right:12px
}
.searchRes span:nth-of-type(1){
    vertical-align: middle;
    margin-right:12px
}
.searchRes span:nth-of-type(2){
    vertical-align: middle;
    padding-top: 1px;
    padding-left: 6px;
    padding-right: 6px;
    padding-bottom: 2px;
    border: 1px solid #b2b2b2;
    border-radius: 6px;
    font-size: 12px;
}
.noneImg{
    height: 105px;
    margin:0 auto;
    margin-top:30px;
    display: block;
}
.noneWord{
    text-align: center;
    font-size: 14px;
    margin-top: 13px;
    color: #999999;
}