.recordWrap{
    position:relative;
}
.recordWrap img{
    width:100%;
}
.typeContent{
    width:85%;
    position:absolute;
    left:30px;
    top:28px;

}
.t_1{
    font-size: 14px;
    color: #333333;
    margin-bottom: 6px;
    font-weight: bold;
}
.t_2{
    margin-bottom:8px;
}
.t_2 span:nth-of-type(1){
    font-size: 12px;
    color: #fff;
    padding-top: 1px;
    padding-left: 6px;
    padding-right: 6px;
    padding-bottom: 1px;
    background: #252525;
    margin-right: 7px;
    border-radius: 5px;
}
.t_2 span:nth-of-type(2){
    font-size:12px;
    color:#fff;
    padding-top:1px;
    padding-left:6px;
    padding-right:6px;
    padding-bottom:1px;
    background:#585858;
    margin-right:5px;
    border-radius: 5px;
}
.t_3{
    color:#5f5f5f;
    font-size:12px;
    margin-bottom:4px;
}
.t_4{
    font-size:14px;
    font-family:PingFang SC;
    font-weight:bold;
    color:rgba(248,5,5,1);
}
.norecord{
    font-size:14px;
    font-family:PingFang SC;
    font-weight:500;
    color:rgba(51,51,51,1);
    text-align: center;
}
.marTop0{
    margin-top: -15px;
}
@media only screen and (max-device-width: 320px) {
    .typeContent{
        top:17px
    }
}