.exitLogin {
    width: 90%;
    height: 44px;
    line-height: 44px;
    color: #fff;
    font-size: 14px;
    text-align: center;
    border-radius: 3px;
    background: #252525;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom:20px;
}
.recomm_type{
    position: relative;
    padding-top: 13px;
    padding-bottom: 7px;
    background: #fff;
    overflow: hidden;
}
.recomm_type_innner{
    width:49.9%;
    text-align: center;
    float:left;
    position:relative;
}
.recomm_type_innner div:nth-of-type(1){
    margin-bottom:4px;
    font-size:12px;
    color:#666;
}
.recomm_type_innner div:nth-of-type(2){
    margin-bottom:4px;
    font-size:12px;
    color:#333;
}
.line{
    position:absolute;
    width:1px;
    height:24px;
    background:#f9f9f9;
    right:0;
    top:10px
}
.recomm_1{
    background:rgba(249,249,248,1);
    font-size:12px;
    font-family:PingFang SC;
    font-weight:500;
    color:rgba(102,102,102,1);
    padding:12px
}
.recomm_share{
    background:#fff;
    padding:14px;
    color:#333333;
    font-size:12px;
    padding-bottom:20px;
    border-bottom:2px solid #f9f9f8;

}
.shareD{
    text-align:center;
    margin-bottom:10px;
}
.shareN div{
    display:inline-block;
    width:80%;
    padding:8px;
    background:rgba(244,244,244,1);
    border-radius:8px;
    font-size:12px;
    font-family:PingFang SC;
    font-weight:500;
    color:rgba(51,51,51,1);
    padding-left:15px;
}
.shareN img{
    width:15px;
    margin-left:10px;
    position: relative;
    top:4px;
}

.adminInnerTitle {
    margin-top: 5px;
    padding-top: 12px;
    text-align: center;
    color: #434343;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 10px;
}

.adminInnerWrap{
    overflow: hidden;
    margin-bottom: 9px;
}
.adminDesDiv{
    width:49.9%;
    float: left;
}
.adminInnerWrap .adminDesDiv:nth-of-type(1){
    border-right:7px solid #fff;
}
.adminInnerWrap .adminDesDiv:nth-of-type(2){
    border-left:7px solid #fff;
}
.adminDesDivTop{
    text-align:center;
    padding-top: 4px;
    padding-bottom: 4px;
    font-size:14px;
    color:#fff;
    background:#252525;
    border-top-left-radius:6px;
    border-top-right-radius:6px;
}
.adminDesDivmiddle{
    padding-top: 4px;
    padding-bottom: 2px;
    padding-left: 8px;
    padding-right: 6px;
    font-size: 12px;
    color: #737373;
    background: #f7f7f7;
}
.adminDesDivmiddle span:nth-of-type(2){
    float:right
}
.spanMoney{
    color:#F80505;
    font-size:12px;
    font-weight: bold;
}
.shop{
    width:13px;
    float:right;
}
.bt{
    border-top:1px solid #e9e9ea;
    border-bottom-left-radius:6px;
    border-bottom-right-radius:6px;
    padding-bottom: 4px;
}
.t0{
    margin-top:10px
}
.rewrap{
    padding-left:10px;
    padding-right: 10px;
    padding-bottom: 15px;
    background: #fff;
}
.fade{
    width: 100%;
    height: 100%;
    position: fixed;
    top:0;
    left:0;
    background: rgba(0,0,0,0.2);
    z-index: 9999;
}
.fade_pop_o{
    border-radius: 8px;
    padding-top: 28px;
    padding-bottom: 32px;
    background: #fff;
    width: 86%;
    position: fixed;
    left: 7%;
    top: 50%;
    margin-top: -150px;
    text-align: center;
    z-index: 99999;
}
.fade_pop_ow1{
    font-size:16px;
    font-family:PingFang SC;
    font-weight:bold;
    color:rgba(51,51,51,1);
    margin-bottom:20px;
}
.fade_pop_owimg1{
    width: 85px;
    height: 85px;
    margin-bottom: 15px;
}
.fade_pop_ow2{
    font-size:12px;
    color:#333;
    margin-bottom:5px;
}
.fade_pop_ow3{
    width: 120px;
    height: 40px;
    background: #292929;
    text-align: center;
    line-height: 40px;
    font-size: 14px;
    color: #fff;
    border-radius: 50px;
    margin: 0 auto;
    margin-top: 18px;
}