.settingAdmin{
    padding-top:11px;
    background: #fff;
}
.adminTitle{
    
    font-size:14px;
    color:#434343;
    font-weight: bold;
    background: #ffffff;

}
.adminDes{
    float:right;
    font-size:12px;
    padding-top: 1px;
    color: #9f9f9f;
}
.adminDes img{
    width:6px;
    margin-left:3px;
    position: relative;
    top:2px;
}
.settingAdminInnerWrap{
    padding-top: 14px;
    padding-bottom:5px;
    overflow:hidden;
}
.settingAdminInner{
    width:33.3%;
    float:left;
    text-align:center;
    position:relative;
}
.settingAdminInner div:nth-of-type(1){
    font-size:14px;
    color: #333333;
    margin-bottom: 5px;
}
.settingAdminInner div:nth-of-type(2){
    font-size:12px;
    color: #9f9f9f;
    margin-bottom: 10px;
}
.lines{
    width: 1px;
    height: 25px;
    background:#d7d7d7 ;
    position: absolute;
    right: 0;
    top: 9px;
}
.borderArea{
    height:5px;
    background: #f9f9f8;
}
.adminMsg{
    background: #fff;
    padding-top:10px;
    padding-bottom: 10px;
    padding-left:12px;
    padding-right: 12px;
}
.adminInnerTitle{
    margin-top:20px;
    text-align:center;
    color:#666666;
    font-size:14px;
    margin-bottom:8px;
}
.adminInnerWrap{
    overflow: hidden;
}
.adminDesDiv{
    width:49.9%;
    float: left;
}
.adminInnerWrap .adminDesDiv:nth-of-type(1){
    border-right:7px solid #fff;
}
.adminInnerWrap .adminDesDiv:nth-of-type(2){
    border-left:7px solid #fff;
}
.adminDesDivTop{
    text-align:center;
    padding-top: 4px;
    padding-bottom: 4px;
    font-size:14px;
    color:#fff;
    background:#252525;
    border-top-left-radius:6px;
    border-top-right-radius:6px;
}
.adminDesDivmiddle{
    padding-top: 4px;
    padding-bottom: 2px;
    padding-left: 8px;
    padding-right: 6px;
    font-size: 12px;
    color: #737373;
    background: #f7f7f7;
}
.adminDesDivmiddle span:nth-of-type(2){
    float:right
}
.spanMoney{
    color:#F80505;
    font-size:12px;
    font-weight: bold;
}
.shop{
    width:13px;
    float:right;
}
.bt{
    border-top:1px solid #e9e9ea;
    border-bottom-left-radius:6px;
    border-bottom-right-radius:6px;
    padding-bottom: 4px;
}
.t0{
    margin-top:10px
}
.fade{
    width: 100%;
    height: 100%;
    position: fixed;
    top:0;
    left:0;
    background: rgba(0,0,0,0.2);
    z-index: 9999;
}
.fade_pop_o{
    border-radius: 8px;
    padding-top: 28px;
    padding-bottom: 32px;
    background: #fff;
    width: 86%;
    position: fixed;
    left: 7%;
    top: 50%;
    margin-top: -150px;
    text-align: center;
    z-index: 99999;
}
.fade_pop_ow1{
    font-size:16px;
    font-family:PingFang SC;
    font-weight:bold;
    color:rgba(51,51,51,1);
    margin-bottom:20px;
}
.fade_pop_owimg1{
    width: 85px;
    height: 85px;
    margin-bottom: 15px;
}
.fade_pop_ow2{
    font-size:12px;
    color:#333;
    margin-bottom:5px;
}
.fade_pop_ow3{
    width: 120px;
    height: 40px;
    background: #292929;
    text-align: center;
    line-height: 40px;
    font-size: 14px;
    color: #fff;
    border-radius: 50px;
    margin: 0 auto;
    margin-top: 18px;
}