.login_wrap{
    width: 100%;
    height: 100%;
    background: #fff;
}
.login_w1{
    font-size: 22px;
    font-family: PingFang SC;
    font-weight: bold;
    color: rgba(41,41,41,1);
    padding-top: 31px;
    padding-left: 22px;
    margin-bottom: 27px;
}
.login_img1{
    width: 100%;
}
.login_div_1{
    position: relative;
}
.login_div_input{
    width: 100%;
    position: absolute;
    top: 145px;
    left:0;
}
.login_img2{
    width: 80px;
    height: 80px;
    position: absolute;
    left:50%;
    top:10px;
    margin-left: -45px;
    
}
input::-webkit-input-placeholder { /* WebKit browsers 适配谷歌 */
    color: #666666
}
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 适配火狐 */
    color: #666666
}
input::-moz-placeholder { /* Mozilla Firefox 19+ 适配火狐 */
    color: #666666
}
input:-ms-input-placeholder { /* Internet Explorer 10+  适配ie*/
    color: #666666
}
.login_div_inputInner{
    width: 75%;
    padding-bottom: 12px;
    margin: 0 auto;
    border-bottom: 1px solid rgba(221,221,221,1);
    margin-bottom: 32px;
    padding-left: 15px;
    position: relative;
}
.login_div_inputInner input{
    color: #666666;
    width: 100%;
}
.loginBtn{
    width: 200px;
    height: 51px;
    background:rgba(41,41,41,1);
    box-shadow:0px 3px 3px 0px rgba(74,74,74,0.3);
    border-radius:51px;
    position: absolute;
    bottom: 10px;
    text-align: center;
    line-height: 51px;
    font-size: 16px;
    left:50%;
    margin-left: -100px;
    color: #fff;
}
.login_title{
    width: 90%;
    margin:0 auto;
    font-size: 12px;
    color: #292929;
    overflow: hidden;
}
.login_title_l{
    display:inline-block;
    float: left;
    color: #d2d2d2;
}
.login_title_r{
    display:inline-block;
    float: right;
    vertical-align: middle;
}
.login_title_r span{
    vertical-align: middle;
    margin-right: 5px;
}
.login_title_r img{
    vertical-align: middle;
    width: 10px;
    height: 10px;
}
.login_des{
    margin-top: 16px;
    margin-bottom:20px;
    text-align: center;
    font-size: 12px;
    color: #d2d2d2;
}
.login_des font{
    display: inline-block;
    vertical-align: middle;
    margin-left: 11px;
    margin-right: 11px;
}
.login_des span{
    display: inline-block;
    width: 100px;
    height: 1px;
    background: #d2d2d2;
    vertical-align: middle;
}
.login_type{
    text-align: center;
}
.login_type img{
    width: 42px;
    height: 42px; 
    display: block;
    margin: 0 auto;
    margin-bottom:2px;
}
.login_type span{
    font-size: 12px;
    color:#666666;
}
.login_title a{
    color: #292929;
}
.cancelBtn{
    text-align: center;
}
.login_img3{
    width: 72px;
    position: absolute;
    left: 12px;
    top: -16px;
}
.codeSpan{
    padding-top: 3px;
    padding-bottom: 4px;
    padding-left: 10px;
    padding-right: 10px;
    background: #000;
    color: #fff;
    font-size: 12px;
    border-radius: 4px;
    position: absolute;
    right: 0;
    top: 0;
}
.cancelBtn{
    color: #292929;
}
.dqImg{
    width: 10px;
    margin-left: 4px;
}
.registLogin_div_input{
    top:96px;
}
.regist_inner{
    margin-bottom: 22px;
}

.fade{
    width: 100%;
    height: 100%;
    position: fixed;
    top:0;
    left:0;
    background: rgba(0,0,0,0.2);
    z-index: 9999;
}
.fade_pop_o{
    border-radius: 8px;
    padding-top: 28px;
    padding-bottom: 32px;
    background: #fff;
    width: 86%;
    position: fixed;
    left: 7%;
    top: 50%;
    margin-top: -150px;
    text-align: center;
    z-index: 99999;
}
.fade_pop_ow1{
    font-size:16px;
    font-family:PingFang SC;
    font-weight:bold;
    color:rgba(51,51,51,1);
    margin-bottom:20px;
}
.fade_pop_owimg1{
    width: 85px;
    height: 85px;
    margin-bottom: 15px;
}
.fade_pop_ow2{
    font-size:12px;
    color:#333;
    margin-bottom:5px;
}
.fade_pop_ow3{
    width: 120px;
    height: 40px;
    background: #292929;
    text-align: center;
    line-height: 40px;
    font-size: 14px;
    color: #fff;
    border-radius: 50px;
    margin: 0 auto;
    margin-top: 18px;
}
.menuDiv {
    width: 100%;
    position: absolute;
    top: 25px;
    left: 0;
    z-index: 9999;
    background: #252525;
}

.menuDiv div{
    padding-top: 3px;
    padding-bottom: 3px;
    border: 1px solid #f9f9f9;
    font-size: 12px;
    color: #ccc;
    text-align: center;
}
@media only screen and (max-device-width: 320px) {
    .registLogin_div_input{
        top:74px!important;
    }
    .regist_inner {
        margin-bottom: 17px!important;
    }
}