.buyTicket{
    text-align: center;
    font-size: 14px;
    color: #252525;
    font-weight: bold;
    /* height: 44px; */
    /* line-height: 44px; */
    padding-top: 20px;
    background: #ffffff;
    
}
.ticketInnerD{
    position: relative;
}
.saleTitle{
    padding-top:1px;
    padding-bottom:1px;
    padding-left:5px;
    padding-right:5px;
    color: #cccccc;
    border:1px solid #ccc;
    
    border-radius: 4px;
    font-size: 12px;
    display: inline-block;
    position: absolute;
    right: 0;
    top: 51px;
}
.dtop{
    margin-top:5px;
}
.ticketDetail{
    padding-top:4px;
    padding-bottom:4px;
}
.ticketDetail span:nth-of-type(1){
    font-size:12px;
    color:#666666;
    margin-right:3px;
    font-weight:bold;
}
.ticketDetail span:nth-of-type(2){
    font-size:12px;
    color:#707070;
    margin-right:3px;
}
.ticketFn{
    padding-top:2px;
    padding-bottom:3px;
    vertical-align: middle;
    padding-bottom: 8px;
}
.ticketFn img{
    height:12px;
    margin-right:2px;
    vertical-align: middle;
}
.ticketFn span{
    color:#999999;
    font-size:12px;
    margin-right:15px;
    vertical-align: middle;
}