
.settingHeader{
    padding-top:15px;
    padding-bottom: 18px;
    border-bottom:1px solid #f8f8f8;
    font-size:14px;
    padding-left:12px;
    vertical-align: middle;
    position: relative;
    background: #fff;
}
.ownImg{
    width:55px;
    height:55px;
    vertical-align: middle;
    margin-right:10px;
    float:left;
}
.personMessage{
    vertical-align: middle;
    margin-left:65px;
    padding-top:9px;
}
.personMessage div:nth-of-type(1){
    color:#434343;
    margin-bottom:3px;
    font-weight: bold;
}
.personMessage div:nth-of-type(2){
    color:#9f9f9f;
    font-size: 12px;
}
.messageEdit{
    position: absolute;
    right: 10px;
    top: 27px;
    text-align: center;
    
}
.messageEdit img{
    width:14px;
    margin-bottom:0px;
}
.messageEdit div{
    font-size:12px;
    color:#9f9f9f;
}
.settingAdmin{
    padding-left:12px;
    padding-right:12px;
    padding-top:11px;
    background: #fff;
}
.adminTitle{
    font-size:14px;
    color:#434343;
    font-weight: bold;

}
.adminDes{
    float:right;
    font-size:12px;
    padding-top: 1px;
    color: #9f9f9f;
}
.adminDes img{
    width:6px;
    margin-left:3px;
    position: relative;
    top:2px;
}
.settingAdminInnerWrap{
    padding-top: 14px;
    padding-bottom:5px;
    overflow:hidden;
}
.settingAdminInner{
    width:33.3%;
    float:left;
    text-align:center;
    position:relative;
}
.settingAdminInner div:nth-of-type(1){
    font-size:12px;
    color: #333333;
    margin-bottom: 5px;
}
.settingAdminInner div:nth-of-type(2){
    font-size:14px;
    color: #9f9f9f;
    margin-bottom: 10px;
}
.lines{
    width: 1px;
    height: 25px;
    background:#d7d7d7 ;
    position: absolute;
    right: 0;
    top: 9px;
}
.borderArea{
    height:5px;
    background: #f9f9f8;
}
.backHeader {
    border-bottom: 2px solid #f9f9f8;
}
.settingInner{
    position: relative;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-left: 15px;
    font-size: 14px;
    color: #4D4D4D;
    border-bottom: 1px solid #f9f9f8;
    vertical-align: middle;
    background: #fff;
}
.settingInner span{
    vertical-align: middle;
}
.limg{
    width: 20px;
    margin-right: 8px;
    vertical-align: middle;
}
.rjt{
    height: 12px;
    position: absolute;
    right: 10px;
    top: 18px;
}
.exitLogin{
    width: 90%;
    height: 44px;
    line-height: 44px;
    color: #fff;
    font-size: 14px;
    text-align: center;
    border-radius: 3px;
    background: #252525;
    margin:0 auto;
    
    margin-top: 20px;
    margin-bottom: 20px;
}
.App{
    background: #f9f9f8;
}
input::-webkit-input-placeholder { /* WebKit browsers 适配谷歌 */
    color: #CCCCCC
}
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 适配火狐 */
    color: #CCCCCC
}
input::-moz-placeholder { /* Mozilla Firefox 19+ 适配火狐 */
    color: #CCCCCC
}
input:-ms-input-placeholder { /* Internet Explorer 10+  适配ie*/
    color: #CCCCCC
}
