.fade{
    width: 100%;
    height: 100%;
    position: fixed;
    top:0;
    left:0;
    background: rgba(0,0,0,0.2);
    z-index: 9999;
}
.fade_pop_o{
    border-radius: 8px;
    padding-top: 28px;
    padding-bottom: 32px;
    background: #fff;
    width: 86%;
    position: fixed;
    left: 7%;
    top: 50%;
    margin-top: -150px;
    text-align: center;
    z-index: 99999;
}
.fade_pop_ow1{
    font-size:16px;
    font-family:PingFang SC;
    font-weight:bold;
    color:rgba(51,51,51,1);
    margin-bottom:20px;
}
.fade_pop_owimg1{
    width: 85px;
    height: 85px;
    margin-bottom: 15px;
}
.fade_pop_ow2{
    font-size:12px;
    color:#333;
    margin-bottom:5px;
}
.fade_pop_ow3{
    width: 120px;
    height: 40px;
    background: #292929;
    text-align: center;
    line-height: 40px;
    font-size: 14px;
    color: #fff;
    border-radius: 50px;
    margin: 0 auto;
    margin-top: 18px;
}
.menuDiv {
    width: 100%;
    position: absolute;
    top: 25px;
    left: 0;
    z-index: 9999;
    background: #252525;
}

.menuDiv div{
    padding-top: 3px;
    padding-bottom: 3px;
    border: 1px solid #f9f9f9;
    font-size: 12px;
    color: #ccc;
    text-align: center;
}