#root{
    width: 100%;
    height: 100%;
  }
  *{
      margin:0;
      padding: 0;
      box-sizing: border-box;
      text-decoration: none;
  }
  .App {
    width: 100%;
    height: 100%;
    font-family: "Arial","Microsoft YaHei","黑体","宋体",sans-serif;
  }
  html,body{
    width: 100%;
    height: 100%;
  }
  a,button,input{-webkit-tap-highlight-color:rgba(255,0,0,0);}
  input{background: none;border:none;outline: none;}
  .backHeader{
    height: 48px;
    line-height: 48px;
    background: #ffffff;
    color: #252525;
    text-align: center;
    position: relative;
    border-bottom: 2px solid #f9f9f8!important;
    font-size: 14px;
    font-weight: bold;
    
    
}
.backImg{
  width: 10px;
  position: absolute;
  left: 15px;
  top: 14px;
}
.backHeader a{
  color: #252525;
}