.message_header{
    width: 54%;
    height: 45px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
.mwrap{
    border-bottom:4px solid #f9f9f8;
    background: #fff;
}
.message_header div{
    display: inline-block;
    height: 45px;
    line-height: 50px;
    color: #bcbcbc;
    font-size: 14px;
    position: relative;
      
}
.message_header div:nth-of-type(1){
    text-align:left;
}
.message_header div:nth-of-type(2){
    text-align:center;
}
.message_header div:nth-of-type(3){
    text-align:right;
}
.mwrap{
    position:relative;
}
.message_headerImg{
    position: absolute;
    right: 16px;
    top: 16px;
    width: 16px;
}
.hW_active{
    color:#252525!important;
}
.hW_active span{
    width: 70%;
    left: 15%;
    height: 3px;
    border-radius: 2px;
    background: #252525;
    bottom: 0;
    position: absolute;
}
.message_content_inner{
    padding-top:15px;
    padding-left:10px;
    padding-right:10px;
    padding-bottom:10px;
    background:#fff;
    overflow:hidden;
    border-bottom:4px solid #f9f9f8;
}
.message_content_innerLeft{
    float:left;
    position: relative;
}
.message_content_innerLeft img{
    width:40px;
    height:40px;
}
.message_content_innerRight{
    margin-left:50px;
}
.message_r_w1{
    position: relative;
}
.message_r_w1{
    color:#252525;
    font-size:14px;
    position:relative;
    margin-bottom:2px;
    font-weight: bold;
}
.focusBtn{
    font-size: 12px;
    color: #d2d2d2;
    position: absolute;
    right: 0;
    top: 0;
    padding-top: 1px;
    padding-left: 6px;
    padding-right: 6px;
    padding-bottom: 2px;
    color: #999999;
    border: 1px solid #999999;
    border-radius: 5px;
    display: inline-block;
}
.message_r_w2{
    font-size:12px;
    color:#c0c0c0;
    margin-bottom:5px;
}
.message_r_w3{
    font-size:12px;
    color:#8b8b8b;
}
.message_content_bottom{
    margin-top: 6px;
    padding-left: 50px;
    vertical-align: middle;
}
.message_content_bottom img{
    height: 14px;
    margin-right: 4px;
    vertical-align: middle;
}
.message_content_bottom span{
    margin-right: 25px;
    color: #cecece;
    font-size: 12px;
    vertical-align: middle;
}
.messageAImg{
    width: 14px!important;
    height: 14px!important;
    position: absolute;
    right: 0;
    top:0;
}
.focusBtnActive{
    background: #252525;
    color: #ffffff;
}

.t8{
    margin-top: 9px;
}
.pimg{
    width: 100%;
    margin-top: 10px;
}
.messagedetail_content_bottom{
    margin-top: 4px;
    padding-left: 0px;
}


.message_content_comment {
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    background: #fff;
    overflow: hidden;
    border-top: 4px solid #f9f9f8;
    border-bottom: 4px solid #f9f9f8;
    
}
.message_content_commentInner{
    padding-bottom:4px;
    border-bottom:1px solid #f9f9f8;
}
.c1{
    color:#252525;
    font-size:16px;
    padding-top:2px;
    padding-bottom:15px;
}
.mi{
    overflow:hidden;
}
.mi_img1{
    float:left;
    width:40px;
    height:40px;
}
.mi{
    margin-left:50px;
}
.mi_w1{
    color:#9f9f9f;
    font-size:12px;
    margin-bottom:3px;
}
.mi_w2{
    color:#464646;
    font-size:12px;
    margin-bottom:3px;
}
.mi_w3{
    color:#9f9f9f;
    font-size:12px;
    margin-bottom:5px;
    vertical-align: middle;
}
.mi_w3 img{
    width:12px;
    margin-right:4px;
    vertical-align: middle;
}
.mi_w3 span{
    color:#9f9f9f;
    font-size:12px;
    vertical-align: middle;
}
.more{
    height:40px;
    line-height: 40px;
    text-align: center;
    font-size: 12px;
    color: #cccbcb;
}
.message_content{
}

.chat{
    width: 90%;
    height: 40px;
    position: relative;
    background: #f6f6f6;
    padding-left: 15px;
    margin: 0 auto;
    border-radius: 40px;
    margin-bottom: 10px;
    margin-top: 10px;
}
.chat input{
    color: #666666;
    width: 100%;
    height: 30px;
    position: relative;
    top: 5px;
}
.c2i{
    width:14px;
    position:absolute;
    right:45px;
    top:13px;
}
 .c1i{
    width:14px;
    position:absolute;
    right:20px;
    top:13px;
}
.fade_pop_ow4{
    width: 120px;
    height: 40px;
    background:rgba(216,216,216,1);
    text-align: center;
    line-height: 40px;
    font-size: 14px;
    color: #fff;
    border-radius: 50px;
    margin: 0 auto;
    margin-top: 11px;
}
.mpHeader{
    position: relative;
}
.mpHeader .mpHeaderBg{
    width:100%
}
.mpHeaderBgBack{
    width: 10px;
    position: absolute;
    top:15px;
    left:12px;
}
.mpfocus{
    position: absolute;
    top: 11px;
    right: 12px;
    color: #fff;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 1px;
    padding-bottom: 1px;
    font-size: 12px;
    border-radius: 3px;
    border: 1px solid #fff;
}
.mpTitle{
    color: #252525;
    font-size: 14px;
    padding-top: 0px;
    padding-bottom: 10px;
    text-align: center;
    font-weight: bold;
}
.mpNum{
    padding-top:10px;
    padding-bottom: 10px;
    overflow: hidden;
}
.mpNumInner{
    width: 49.9%;
    float: left;
    text-align: center;
    margin-bottom: 4px;
    color:#616161;
    position: relative;
    font-size: 12px;
    font-weight: bold;
}
.line{
    width: 1px;
    height:20px;
    background: #c0c0c0;
    position: absolute;
    top:7px;
    right:0
}

.mpNumInner div{
    margin-bottom: 2px;
}

.mpSlider{
    padding-top: 12px;
    padding-bottom: 13px;
    padding-left: 15px;
    padding-right: 15px;
    background: #f9f9f8;
    font-size: 12px;
    color: #666666;
    width: 100%;
    overflow-y: hidden;
    overflow-x: scroll;
    white-space: nowrap;
}
.mpSlider div{
    display: inline-block;
    margin-right: 30px;
}
.sliderActive{
    color: #000;
}
.sw{
    padding-bottom:5px;
}
.sliderActive .sw{
    border-bottom: 2px solid #000;
    border-radius: 2px;
}