.ticketWrap{
    background: #fff;
    border-top: 20px solid #f7f7f7;
}
.ticketInner{
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 8px;
    border-bottom: 1px solid #f3f3f3;
}
.ticketInnerL{
    height: 95px;
    position: relative;
    top: -17px;
    float: left;
}
.ticketInnerR{
    float:left;
    margin-left:10px;
}
.ticketInnerR div:nth-of-type(1){
    color:#252525;
    font-size:14px;
    margin-bottom: 11px;
    font-weight:bold;
}
.ticketInnerR div:nth-of-type(2){
    color:#252525;
    font-size:12px;
    color:#878787;
    vertical-align: middle;
}
.ticketInnerR div:nth-of-type(2) img{
    vertical-align: middle;
    width: 23px;
    margin-right: 6px;
    margin-bottom: 1px;
}                
.ticketInnerR div:nth-of-type(2) span{
     vertical-align: middle;
}
.ticketInnerR div:nth-of-type(3) img{
    width: 36px;
    margin-top: 3px;
}
.clear{
    clear: both;
}
.ticDes{
    padding-top:3px;
    padding-bottom:3px;
    vertical-align: middle;
}
.ticDes img{
    width:14px;
    vertical-align: middle;
    margin-right:5px;
}
.ticDes span{
    vertical-align: middle;
    font-size:12px;
    color:#acacac;
}
.typeImg{
    position:relative;
}
.typeContent{
    position:absolute;
    left:20px;
    top:26px;
    width:85%;
}
.t_1{
    margin-bottom:10px;
}
.t_1 span:nth-of-type(1){
    font-size:14px;
    color:#333333;
    font-weight:bold;
}
.t_1 span:nth-of-type(2){
    font-size:12px;
    color:#999999;
    float:right;
    position: relative;
    top: 5px;
}
.t_2{
    margin-bottom:5px;
    font-size:12px;
    color:#666666
}
.typeImg img{
    width: 100%;
}
.dline{
    width: 100%;
    height: 1px ;
    background: #f7f7f7;
    margin-bottom: 5px;
}
@media only screen and (max-device-width: 320px) {
    .typeContent{
        top:20px
    }
}